.banner {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 220px;
  margin-bottom: 300px;
  font-family: "courier", "georgia", "palatino";
}
.banner__container {
  text-align: center;
  margin: 0 auto;
  min-width: 300px;
}
.banner__container__background {
  width: 200px;
  height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 0 auto 20px auto;
  /* animation: rotate 3s linear infinite; */
}

@-webkit-keyframes rotate {
  100% {
    transform: rotate(1turn);
  }
}

@keyframes rotate {
  100% {
    transform: rotate(1turn);
  }
}

.banner__container .banner__container__background > img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: contain;
}

.banner__container .banner__container__background::before {
  box-sizing: border-box;
  content: "";
  position: absolute;
  z-index: -2;
  width: 220px;
  height: 220px;
  background-color: #39998a;
  background-repeat: no-repeat;
  background-size: 100%;
  border-radius: 50%;
  background-position: 0 0, 20% 0, 20% 20%, 0 20%, 30% 30%;
  background-image: linear-gradient(
    red,
    orange,
    yellow,
    green,
    #ff00ff,
    blue,
    violet
  );
  -webkit-animation: rotate 3s linear infinite;
          animation: rotate 3s linear infinite;
}

.banner__container > h1 {
  font-size: 46px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-bottom: 10px;
  -webkit-animation: RGBshift 3s infinite alternate;
  animation: RGBshift 2s infinite alternate;
}
.socialMedia__links {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.socialMedia__links > a {
  display: inline-block;
  font-size: 36px;
  margin-top: 10px;
  color: #000;
  margin-right: 10px;
}

@-webkit-keyframes RGBshift {
  0% {
    transform: rotate(0deg);
    text-shadow: 5px 10px 0px rgb(245, 245, 241);
    opacity: 1;
  }
  6% {
    transform: rotate(0.5deg) scale(1.01);
    text-shadow: 1px 1px 0px rgba(255, 0, 0, 0.5),
      -5px -2px 0px rgba(0, 255, 0, 0.5), -2px 4px 0px rgba(0, 0, 255, 0.5);
    opacity: 1;
  }
  10% {
    transform: rotate(0deg);
    text-shadow: 8px -2px 0px rgba(255, 0, 0, 0.5),
      -5px -2px 0px rgba(0, 255, 0, 0.5), 2px -2px 0px rgba(0, 0, 255, 0.5);
    opacity: 0.6;
  }
  12% {
    text-shadow: none;
    opacity: 1;
  }
  58% {
    transform: rotate(0deg);
    text-shadow: none;
    opacity: 1;
  }
  59% {
    text-shadow: -5px -8px 0px rgb(245, 245, 241);
    opacity: 0.6;
  }
  62% {
    transform: rotate(-0.5deg) scale(1.01);
    text-shadow: 8px -2px 0px rgba(255, 0, 0, 0.5),
      -5px -2px 0px rgba(0, 255, 0, 0.5), 2px -2px 0px rgba(0, 0, 255, 0.5);
    opacity: 1;
  }
  64% {
    transform: rotate(-0.75deg) scale(1.01);
  }
  67% {
    text-shadow: none;
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes RGBshift {
  0% {
    transform: rotate(0deg);
    text-shadow: 5px 10px 0px rgb(245, 245, 241);
    opacity: 1;
  }
  6% {
    transform: rotate(0.5deg) scale(1.01);
    text-shadow: 1px 1px 0px rgba(255, 0, 0, 0.5),
      -5px -2px 0px rgba(0, 255, 0, 0.5), -2px 4px 0px rgba(0, 0, 255, 0.5);
    opacity: 1;
  }
  10% {
    transform: rotate(0deg);
    text-shadow: 8px -2px 0px rgba(255, 0, 0, 0.5),
      -5px -2px 0px rgba(0, 255, 0, 0.5), 2px -2px 0px rgba(0, 0, 255, 0.5);
    opacity: 0.6;
  }
  12% {
    text-shadow: none;
    opacity: 1;
  }
  58% {
    transform: rotate(0deg);
    text-shadow: none;
    opacity: 1;
  }
  59% {
    text-shadow: -5px -8px 0px rgb(245, 245, 241);
    opacity: 0.6;
  }
  62% {
    transform: rotate(-0.5deg) scale(1.01);
    text-shadow: 8px -2px 0px rgba(255, 0, 0, 0.5),
      -5px -2px 0px rgba(0, 255, 0, 0.5), 2px -2px 0px rgba(0, 0, 255, 0.5);
    opacity: 1;
  }
  64% {
    transform: rotate(-0.75deg) scale(1.01);
  }
  67% {
    text-shadow: none;
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

.socialMedia__links > a:hover {
  transform: scale(1.6);
  color: red;
  transition: 0.3s;
}

.about-wrapper {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  text-align: center;
  width: 100%;
  padding: 4em 0;
}
.about > * {
  padding: 1em;
}

.about-title-smallscreen {
  display: none;
}
.title-gray {
  text-align: center;
  margin-bottom: 50px;
  margin: auto auto;
  width: 200px;
  padding: 10px;
  border-bottom: 6px solid rgba(78, 78, 255, 2);
}
.intro {
  text-align: left;
  overflow: hidden;
  padding-left: 2em;
  justify-content: center;
  text-align: center;
}
.about-paragraph {
  color: #787878;
  line-height: 1.5;
  font-size: 20px;
  letter-spacing: 0.4px;
  word-spacing: 1px;
  text-align: center;
  margin: 2em auto;
  max-width: 700px;
}
.about-title {
  font-size: 30px;
  font-weight: 400;
  line-height: 1.4;
  letter-spacing: 1px;
  word-spacing: 2px;
  font-family: "CoHeadlineW01-Regular";
}

.projects {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  text-align: center;
  align-items: center;
  padding: 20px 20px;
  margin-top: 50px;
  margin-bottom: 50px;
}

.projects > h1 {
  margin-bottom: 50px;
  width: 200px;
  padding: 10px;
  border-bottom: 6px solid rgba(78, 78, 255, 2);
}

.projects .projects__container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 40px;
  background-color: white;
  text-align: left;
}

.projects__project {
  position: relative;
  display: flex;
  flex-shrink: 1;
  margin-bottom: 30px;
  flex-direction: column;
  background-color: white;
  z-index: 1;
  width: 45%;
  margin: 10px;
  float: left;
  border: 1px solid blue;
  padding: 0px 0px 0px 0px;
  border-radius: 20px;
  max-height: 500px;
  min-width: 260px;
}
.project__project__description {
  padding: 30px 20px;
}

.projects__project__thumbnail {
  position: relative;
  max-height: 320px;
  min-height: 320px;
  object-fit: contain;
  width: 100%;
}
.projects__project__thumbnail > img {
  height: 100%;
  width: 100%;
  object-fit: contain;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.projects__project__thumbnail > .projects__project__overlay {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  top: 0;
  left: 0;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  width: 100%;
  z-index: 2;
  transition: 0.5s ease;
  background: black;
  opacity: 0;
}

.projects__project__thumbnail:hover .projects__project__overlay {
  opacity: 0.7;
}
.projects__project__thumbnail:hover .projects__project__overlayBtns {
  visibility: visible;
}

.projects__project__overlayBtns {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: auto auto;
  z-index: 3;
  visibility: hidden;
}
.projects__project__overlayBtns > a {
  display: inline-block;
  position: relative;
  left: -50%;
  font-size: 16px;
  color: #000;
  text-decoration: none;
  margin-right: 15px;
}
.projects__project__overlayBtns > a:link {
  color: red;
}
.projects__project__overlayBtns > a span {
  background: #ffd43a;
  color: #2d1863;
  height: 50px;
  line-height: 50px;
  padding: 20px;
  width: 50px;
  font-size: 18px;
  border-radius: 30px;
  transition: 300ms;
}

