/* ================ The Timeline ================ */

.timeline {
  position: relative;
  width: 800px;
  margin: 0 auto;
  padding: 1em 0;
  list-style-type: none;
}

.timeline::before {
  position: absolute;
  left: 0;
  right: 0;
  top: -3%;
  margin: auto auto;
  content: " ";
  display: block;
  width: 6px;
  height: 100%;
  background: rgb(80, 80, 80);
  background: linear-gradient(
    to bottom,
    rgba(80, 80, 80, 0) 0%,
    rgb(80, 80, 80) 8%,
    rgb(80, 80, 80) 92%,
    rgba(80, 80, 80, 0) 100%
  );
}

.timeline li {
  position: relative;
  width: 100%;
  margin-bottom: 50px;
}

.timeline li::after {
  content: "";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}

.direction-l {
  position: relative;
  float: left;
  text-align: right;
}

.direction-l,
.direction-r {
  width: 340px;
}
.direction-r {
  position: relative;
  float: right;
  text-align: left;
}

.flag-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  /* text-align: center; */
}

.flag {
  position: relative;
  display: inline;
  background: rgb(248, 248, 248);
  padding: 6px 10px;
  border-radius: 5px;

  font-weight: 600;
  text-align: left;
}

.direction-l .flag {
  box-shadow: -1px 1px 1px rgba(0, 0, 0, 0.15), 0 0 1px rgba(0, 0, 0, 0.15);
}

.direction-r .flag {
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15), 0 0 1px rgba(0, 0, 0, 0.15);
}

li::before {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto auto;
  content: " ";
  display: block;
  width: 20px;
  height: 20px;
  background: #fff;
  border-radius: 50%;
  border: 4px solid rgb(255, 219, 12);
  z-index: 17;
}

.time-wrapper {
  display: inline;

  line-height: 1em;
  font-size: 0.86666em;
  color: rgb(250, 80, 80);
  vertical-align: middle;
}

.direction-l .time-wrapper {
  float: left;
}

.direction-r .time-wrapper {
  float: right;
}

.time {
  display: inline-block;
  padding: 4px 6px;
  background: rgb(248, 248, 248);
}

.desc {
  margin: 1em 0.75em 0 0;
  color: #787878;
  font-size: 1em;
  font-style: italic;
  line-height: 1.5em;
}

.direction-r .desc {
  margin: 1em 0 0 0.75em;
}

/* ================ Timeline Media Queries ================ */

@media screen and (max-width: 660px) {
  .timeline {
    width: 100%;
  }

  .timeline li {
    margin: 100px 0;
  }
  .timeline li::before {
    top: -50px;
  }
  .direction-l,
  .direction-r {
    float: none;
    width: 100%;

    text-align: center;
  }

  .flag-wrapper {
    text-align: center;
  }

  .flag {
    text-align: center;
    background: rgb(255, 255, 255);
    z-index: 15;
  }

  .time-wrapper {
    display: block;
    position: relative;
    margin: 4px 0 0 0;
    z-index: 14;
  }

  .direction-l .time-wrapper {
    float: none;
  }

  .direction-r .time-wrapper {
    float: none;
  }

  .desc {
    position: relative;
    margin: 1em 0 0 0;
    padding: 1em;
    background: rgb(245, 245, 245);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0.2);

    z-index: 15;
  }

  .direction-l .desc,
  .direction-r .desc {
    position: relative;
    margin: 1em 1em 0 1em;
    padding: 1em;

    z-index: 15;
  }
}

